/* src/components/Contact.css */

.contact-section {
  padding: 80px 20px;
  background: var(--bg-color);
  color: var(--text-color);
  text-align: center;
  overflow: hidden;
}

.contact-section h2 {
  font-size: 2.75rem;
  margin-bottom: 50px;
  color: var(--accent-color);
  text-shadow: 0 0 5px rgba(100, 255, 218, 0.2);
}

/* === LAYOUT === */
.contact-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
  animation: fadeInUp 1s ease forwards;
}

/* === FORM === */
.contact-form {
  width: 100%;
  max-width: 520px;
  display: flex;
  flex-direction: column;
  gap: 18px;
  background: var(--card-bg-color);
  color: var(--text-color);
  padding: 30px;
  border-radius: 15px;
  backdrop-filter: blur(8px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
}

.contact-form input,
.contact-form textarea {
  padding: 14px;
  border: 1px solid var(--input-border-color);
  border-radius: 8px;
  background: var(--input-bg-color);
  color: var(--text-color);
  font-size: 1rem;
  transition: all 0.3s ease;
}

.contact-form input:focus,
.contact-form textarea:focus {
  outline: none;
  border-color: var(--accent-color);
  box-shadow: 0 0 5px rgba(100, 255, 218, 0.3);
}

.contact-form button {
  background: var(--accent-color);
  color: var(--button-text-color);
  border: none;
  padding: 14px;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.contact-form button:hover {
  background: var(--accent-color-hover);
  transform: translateY(-2px) scale(1.02);
  box-shadow: 0 6px 18px rgba(100, 255, 218, 0.3);
}

/* === CONTACT INFO === */
.contact-info {
  width: 100%;
  max-width: 520px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  text-align: left;
  color: var(--muted-text-color);
  font-size: 1rem;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 18px;
  border-radius: 10px;
  transition: all 0.3s ease;
}

.contact-item:hover {
  background: var(--hover-bg-color);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(100, 255, 218, 0.1);
}

.contact-item a {
  color: var(--accent-color);
  text-decoration: none;
  position: relative;
}

.contact-item a::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: -2px;
  left: 0;
  background-color: var(--accent-color);
  transition: width 0.3s;
}

.contact-item a:hover::after {
  width: 100%;
}

.icon {
  font-size: 1.25rem;
  color: var(--accent-color);
  transition: transform 0.3s ease;
}

.contact-item:hover .icon {
  transform: scale(1.2);
}

/* === ANIMATION === */
@keyframes fadeInUp {
  from {
    transform: translateY(40px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
