.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Responsive Layout */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

/* Grid defaults */
.skills-grid,
.projects-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.btn {
  padding: 10px 18px;
  border-radius: 6px;
  /* background-color: #0078d4;
  color: white; */
  background-color: var(--accent-color);
  color: var(--bg-color);
  font-weight: bold;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn:hover {
  /* background-color: #005ea6; */
  background-color: var(--text-color);
  color: var(--bg-color);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(0, 94, 166, 0.3);
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .hero-content,
  .about-section,
  .skills-container,
  .projects-container,
  .contact-section {
    padding: 20px;
    flex-direction: column;
    text-align: center;
  }

  .skills-grid,
  .projects-grid {
    grid-template-columns: 1fr !important;
  }

  .project-card {
    margin: 20px auto;
    width: 90%;
  }
}

:root {
  --bg-color: #ffffff;
  --text-color: #111111;
  --accent-color: #0078d4;
  --card-bg: #f5f5f5;
  --card-shadow: rgba(0, 0, 0, 0.1);
}

[data-theme='dark'] {
  --bg-color: #0f172a;
  --text-color: #f8f8f8;
  --accent-color: #38bdf8;
  --card-bg: #1e293b;
  --card-shadow: rgba(255, 255, 255, 0.05);
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
  transition: background-color 0.3s ease, color 0.3s ease;
}

* {
  transition: background-color 0.3s ease, color 0.3s ease;
}
